html {
  height: 100%;
}
.page-body {
  min-width: 320px;
  height: 100%;
  margin: 0 auto;
  background-image: url(../img/back-mobile.jpg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: top;
}
.page-header {
  position: relative;
  width: auto;
  height: 66px;
  padding: 22px 20px 21px;
  color: white;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
}
.logo {
  display: inline-block;
  width: auto;
  height: 23px;
}
.popup-menu {
  position: absolute;
  display: none;
  top: 0px;
  left: 0px;
  width: 100%;
  background-color: #283645;
}
.page-main {
  color: white;
}
.page-header__toggler {
  height: 100%;
  border: none;
}
.download__title {
  margin: 0px;
  text-align: center;
  font-family: inherit;
  font-style: inherit;
  font-size: 24px;
  font-weight: 300;
  line-height: 28.8px;
}
.download__wrapper {
  position: relative;
  height: 667px;
  padding: 20px 0px;
}
.download__header-image {
  margin: 0 auto;
  margin-top: 34px;
}
.download__header-screen {
  position: absolute;
  top: 115px;
  left: 104px;
}
body {
  font-family: "Open Sans", sans-serif;
  font-style: normal;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
